.home .bar {
  position: fixed;
  z-index: 10;
  left: 0;
  width: 100%;
  top: 0;
  height: 50px;
  line-height: 50px;
  font-family: PingFangSC-Semibold, sans-serif;
  color: #ffffff;
  background-color: #d81e06;
  padding: 0 10px;
}
.home .bar .bar-title {
  font-size: 1.5em;
}
.home .bar .bar-tips {
  font-size: 1em;
}
.home .scroll {
  margin-top: 50px;
}
.home .scroll .container {
  margin-bottom: 100px;
}
.home .scroll .container .cards {
  padding: 5px;
}
.home .scroll .container .cards .weui-panel {
  padding: 5px;
  border: 1px solid #f8f2f2;
}
.home .scroll .container .cards .card-img {
  width: 100%;
  height: 200px;
}
.home .scroll .container .cards .title {
  color: #696969;
  font-size: 1.1em;
}
.home .scroll .container .cards .stock {
  color: #999;
  font-size: 0.8em;
}
.home .scroll .container .cards .time {
  color: #999;
  font-size: 0.9em;
}
.home .scroll .container .cards .time span {
  color: #d81e06;
}
.home .scroll .container .cards .bottom {
  width: 100%;
  border-top: 1px solid #f8f2f2;
  height: auto;
}
.home .scroll .container .cards .bottom .price {
  float: left;
  color: red;
  font-size: 1.4em;
}
.home .scroll .container .cards .bottom .price span {
  text-decoration: line-through;
  font-size: 0.6em;
}
.home .scroll .container .cards .bottom .sell {
  float: right;
  line-height: 2.4em;
  padding-right: 6px;
  font-size: 0.8em;
  color: #696969;
}
.home .scroll .container .cards .footer {
  border-top: 1px solid #f8f2f2;
  margin-top: 2.5em;
}
.home .scroll .container .cards .footer .add-btn {
  width: 100%;
  border-radius: 10px;
  padding: 2px 15px;
  color: #fff;
  background-color: #2cb22f;
}



