.pro-detail {
  margin-bottom: 100px;
}
.pro-detail .pro-content {
  font-family: PingFangSC-Semibold, sans-serif;
}
.pro-detail .pro-content .content-header {
  background-color: #fff;
}
.pro-detail .pro-content .content-header p {
  padding: 0 5px;
}
.pro-detail .pro-content .content-header .title {
  color: #333333;
  font-size: 1.1em;
}
.pro-detail .pro-content .content-header .detail {
  color: #999999;
  font-size: 0.8em;
}
.pro-detail .pro-content .content-option .vux-label,
.pro-detail .pro-content .content-option .weui-cell__ft,
.pro-detail .pro-content .content-option .vux-cell-value,
.pro-detail .pro-content .content-option .weui-label,
.pro-detail .pro-content .content-option .weui-cell {
  color: #666;
  font-size: 14px;
}
.pro-detail .pro-content .price-time {
  width: 100%;
  height: 50px;
  display: flex;
  display: -webkit-flex;
  justify-content: space-around;
  align-items: center;
}
.pro-detail .pro-content .price-time .price {
  width: 60%;
  height: 50px;
  color: #fff;
  background-color: #d81e06;
  display: flex;
  display: -webkit-flex;
  justify-content: space-around;
  align-items: center;
}
.pro-detail .pro-content .price-time .price .new-price {
  float: left;
  font-size: 12px;
}
.pro-detail .pro-content .price-time .price .new-price span {
  font-size: 20px;
}
.pro-detail .pro-content .price-time .price .old-price {
  font-size: 12px;
  text-decoration: line-through;
}
.pro-detail .pro-content .price-time .time {
  width: 40%;
  background-color: #eebc11;
  height: 50px;
}
.pro-detail .pro-content .price-time .time p {
  font-size: 12px;
  text-align: center;
  color: #666;
  line-height: 20px;
}
.pro-detail .pro-content .price-time .time .time-real {
  font-size: 16px;
  line-height: 30px;
  color: #333;
}
.bottom {
  position: fixed;
  z-index: 10;
  bottom: 0;
  left: 0;
  right: 0;
  height: 60px;
  background-color: #fff;
  display: flex;
  display: -webkit-flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: nowrap;
}
.bottom .bottom-item {
  position: relative;
  height: 100%;
}
.bottom .bottom-item img {
  width: 30px;
}
.bottom .bottom-item .vux-badge {
  position: absolute;
  top: 5px;
  right: 10px;
}
.bottom .bottom-flex {
  display: flex;
  display: -webkit-flex;
  justify-content: center;
  align-items: center;
}
.bottom .bottom-width-2 {
  width: 30%;
}
.bottom .bottom-width-4 {
  width: 35%;
  text-align: center;
  line-height: 60px;
  color: #fff;
}
.bottom .bottom-width-8 {
  width: 70%;
}
.bottom .bottom-none {
  color: #fff;
  background-color: #a9a39d;
}
.bottom .bottom-cart {
  background-color: #ec7b0c;
}
.bottom .bottom-buy {
  background-color: #f82206;
}
.bottom .bottom-load {
  background-color: #ea4833;
}
.bottom .bottom-load .vux-spinner {
  fill: #fff;
}



