@import url('assets/iconfont/iconfont.css');
.slide-enter-active,
.slide-leave-active {
  transition: all 0.2s ease;
}
.slide-enter,
.slide-leave {
  opacity: 0;
}
body {
  background-color: #f2f2f2;
}
.weui-tabbar {
  position: fixed;
  z-index: 50;
}
.weui-tabbar__item {
  text-decoration: none;
}
.vux-header {
  background-color: #515151 !important;
}



